.app {
  height: 100vh;
}

.app > .sections {
  width: 100%;
  height: calc(100vh - 70px);
  background-color: pink;
  position: relative;
  top: 70px;
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory;
}

.app > .sections::-webkit-scrollbar {
  display: none;
}

.app > .sections > * {
  width: 100vw;
  height: calc(100vh - 70px);
  scroll-snap-align: start;
}
